.box {
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.input {
  margin: 0;
  padding: 10px;
  min-width: 300px;
  height: 40px;
  background-color: #fbf9fa3f;
  color: black;
  font-size: 20px;
  border-radius: 5px 0 0 5px;
}
.input:active {
  outline: 1px solid pink;
}
.input:focus {
  outline: 1px solid pink;
}
.btn {
  height: 64px;
  width: 57px;
  background-color: #fbf9fa3f;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
}
.moviesText {
  color: black;
  font-size: 20px;
  font-weight: 700px;
}
